body {
  background-color: #9266ff;
}
.App {
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}
#to-do-list input {
  background-color: rgb(27,112,137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #fff;
}
#to-do-list input::placeholder {
  color: rgb(255,255,255,0.5);
}
#to-do-list input:focus , #to-do-list button:focus{
  outline: none;
}
#to-do-list button {
  background-color: rgb(202,156,24);
  border-radius: 10px;
  width: 80px;
  height: 50px;  
  border: 0;
  cursor: pointer;
  color: #fff;
  font-weight: bold;
}